import React,{ useState } from 'react';
import swal from 'sweetalert';
import { Link,Redirect } from "react-router-dom";
const Register = () => {
    const [fNameValue, setterFName ] = useState('');
    const [lNameValue, setterlName ] = useState('');
    const [emailValue, setterEmail ] = useState('');
    const [passwordValue, setterPassword ] = useState('');
    const [cnfrmPasswordValue, setterCnfPassword ] = useState('');
    
    var sessionUser = localStorage.getItem("userId");
     //* change the value of of text field and set the value in new variable */
     const setFirstName  = (event) =>{
        setterFName(event.target.value);   
    }
    const setLastName  = (event) =>{
        setterlName(event.target.value);   
    }
    const setEmail  = (event) =>{
        setterEmail(event.target.value);   
    }
    const setPassword  = (event) =>{
        setterPassword(event.target.value);   
    }
    const setCnfPassword  = (event) =>{
        setterCnfPassword(event.target.value);   
    }

    const submitRegister = (event) =>{
        event.preventDefault();
        const requestOptions = {
            method: 'post',
            body: JSON.stringify({ "fname":fNameValue,"lname":lNameValue,"email":emailValue,"password":passwordValue}),
            headers: {
                Accept: 'application/json',
                // crossDomain : true,
                'Content-Type': 'application/json'
                
            } 
           
        }
        fetch("http://localhost:3001/api/registerUser", requestOptions)
            .then(response => response.json())
            .then((data) => {
              //   console.log(data);
                
                if(data.status === 'success'){
                    swal("Thanks! Your details has been submitted", "", "success");
                     setterFName('');
                     setterlName('');
                     setterEmail('');
                     setterPassword('');
                     setterCnfPassword('');
                }
                else{
                    swal("Sorry! Please try again", "", "error");
                }
                
            });

      }
    
      return(
        <>
        {
                sessionUser ? <Redirect to = "/home"></Redirect> : null 
        }
        <form onSubmit={ submitRegister }>
        <div className="container">
            <div className="row">
              <div className="col-md-12">
                <h1>Register</h1>
                    <p>Please fill in this form to create an account.</p>
                    <hr/>
              </div>
            </div>
           
            <div className="row">
               <div className="col-md-12">
                <label htmlFor="fname"><b>First Name</b></label>&nbsp;
                <input type="text" className="form-control" placeholder="First Name" onChange={setFirstName} value={fNameValue} name="fname" id="fname" required/>
               </div>
             </div>

             <div className="row">
               <div className="col-md-12">
                <label htmlFor="lname"><b>Last Name</b></label>&nbsp;
                <input type="text" className="form-control" placeholder="Last Name" onChange={setLastName} value={lNameValue} name="lname" id="lname" required/>
               </div>
             </div>


            <div className="row">
               <div className="col-md-12">
                <label htmlFor="email"><b>Email</b></label>&nbsp;
                <input type="Email" className="form-control" placeholder="Enter Email" onChange={setEmail}  value={emailValue} name="email" id="email" required/>
               </div>
             </div>

             <div className="row">
               <div className="col-md-12">
                   <label htmlFor="psw"><b>Password</b></label>&nbsp;
                    <input type="password" className="form-control" placeholder="Enter Password" onChange={setPassword} value={passwordValue} name="psw" id="psw" required/>
               </div>
             </div>

             <div className="row">
               <div className="col-md-12">
                <label htmlFor="psw-repeat"><b>Repeat Password</b></label>&nbsp;
                <input type="password" className="form-control" placeholder="Repeat Password" onChange={setCnfPassword} value={cnfrmPasswordValue} name="psw-repeat" id="psw-repeat" required/>
               <hr/>
                </div>
            </div>

            <button type="submit" className="registerbtn">Register</button>
        </div>
        </form>

        <div className="container signin">
            <p>Already have an account? <Link to="/login">Sign in</Link>.</p>
        </div>
         
        </>
    );

}

export default Register;