import React,{ useState } from 'react';
import swal from 'sweetalert';
// import { ReactSession } from 'react-client-session';
import { useHistory,Link,Redirect  } from "react-router-dom";
import PropTypes from "prop-types";



const Login = () => {
// class Login extends Component {

    // history = null;
    // static propTypes = {
    //    history: PropTypes.object.isRequired
    // };
    
    const history = useHistory();


    
    const [emailValue, setterEmail ] = useState('');
    const [passwordValue, setterPassword ] = useState('');
   
    /* change the value of of text field and set the value in new variable */
    const setEmail  = (event) =>{
        setterEmail(event.target.value);   
    }
    const setPassword  = (event) =>{
        setterPassword(event.target.value);   
    }

    
    // render(){
        // const { history } = this.props;
        
        // ReactSession.set("userId", "");
        // ReactSession.set("userEmail", "");
        // ReactSession.set("userName", "");

        var sessionUser = localStorage.getItem("userId");

        const submitLogin = (event) =>{
            event.preventDefault();
            
            const requestOptions = {
                method: 'post',
                body: JSON.stringify({"email":emailValue,"password":passwordValue}),
                headers: {
                    Accept: 'application/json',
                    // crossDomain : true,
                    'Content-Type': 'application/json'
                    
                } 
               
            }
            fetch("http://localhost:3001/api/LoginUser", requestOptions)
                .then(response => response.json())
                .then((data) => {
                    if(data.status === 'success'){
                        let userID = data.data[0].id;
                        let userEmail = data.data[0].email;
                        let userName = data.data[0].first_name+' '+data.data[0].last_name;   
                        localStorage.setItem("userId",userID)
                        localStorage.setItem("userEmail",userEmail)
                        localStorage.setItem("userName",userName)
                        swal("Successfully Login", "", "success");
                        history.push("/screenrecorder");
                    }
                    else{
                        swal("Sorry! Please try again", "", "error");
                    }
                    
                });
    
        }

        return(
            <>
            {
                sessionUser ? <Redirect to = "/screenrecorder"></Redirect> : null 
            }
            <form onSubmit={ submitLogin }>
            <div className="container">
                <div className="row">
                  <div className="col-md-12">
                  
                    <h1>Login</h1>
                        <p>Please fill in this form to Login account.</p>
                        <hr/>
                  </div>
                </div>
                
                <div className="row">
                   <div className="col-md-12">
                    <label htmlFor="email"><b>Email</b></label>&nbsp;
                    <input type="Email" className="form-control" placeholder="Enter Email" onChange={setEmail} value={emailValue} name="email" id="email" required/>
                   </div>
                 </div>
    
                 <div className="row">
                   <div className="col-md-12">
                       <label htmlFor="psw"><b>Password</b></label>&nbsp;
                        <input type="password" className="form-control" placeholder="Enter Password" onChange={setPassword} value={passwordValue} name="psw" id="psw" required/>
                   </div>
                 </div>
                  <button type="submit" className="loginbtn btn btn-primary">Login</button>
            </div>
            </form>
    
            <div className="container signin">
                <p>Register an account? <Link to='/register'>Sign Up</Link>.</p>
            </div>
             
            </>
        )
    // }
}

export default Login;