import React,{ useState,useEffect } from 'react';
import swal from 'sweetalert';
// import { ReactSession } from 'react-client-session';
// import PublicUrl from './Public';
import Home from "./Home";
const GetVideos = () => {
    const [videos, setVideos] = useState(null);
     let sessionUserId = localStorage.getItem("userId");
     
     let x = 1;
     useEffect(() => {
      
     getVideos();
     async function getVideos() {
        
        const requestOptions = {
            method: 'post',
            body: JSON.stringify({ "user":sessionUserId}),
            headers: {
                Accept: 'application/json',
                // crossDomain : true,
                'Content-Type': 'application/json'
                
            } 
           
        }
        await fetch("http://localhost:3001/api/getVideos", requestOptions)
            .then(response => response.json())
            .then((data) => {
              //   console.log(data);
                if(data.status === 'success'){
                    let videodata = data.data;
                    setVideos(videodata) ;
                   
                    
                }
                else{
                    swal("Sorry! Please try again", "", "error");
                }
                
            });

      }
    }, []);


    function shareUrl(e){
      var url  = e.target.id;
      navigator.clipboard.writeText(url)
      swal("Successfully copied !", "", "success");
    }
    
      return(
        <>
        <Home/>
        <div className="container">
            <div className="row">
              <div className="col-md-12">
                <h1>User Videos</h1>
                    <p>User Screen and web-Cam recordings.</p>
                    <hr/>
              </div>
            </div>
           
            <div className="row">
               <div className="col-md-12">
                  <table>
                  
                   <thead>
                    <tr>
                        <th>Sr.No</th>
                        <th className="table_mr">Video</th>
                        <th className="table_mr">Total User View</th>
                        <th>Created Date</th>
                        <th>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                    {videos && (
                        
                      <>
                      {videos.map((video, index) => (   
                        <tr key={`tr-key`+index}>
                            <td key={`td1-key`+index}>
                              {x++}
                            </td>
                            <td key={`td2-key`+index}>
                            <video className="video_mr" width="250" height="200" controls>
                            
                                <source src={`/`+video.video_url} type="video/mp4"/>
                                </video>
                                </td>
                            <td key={`td5-key`+index}>{video.total_user_view}</td>   
                            <td key={`td3-key`+index}>{video.created_date}</td>
                            <td key={`td4-key`+index}><button type="button" className="btn btn-primary" onClick={shareUrl} id={window.location.origin+'/preview/'+video.id+'/'+video.video_url}>Share</button></td>
                        </tr>
                      ))}
                      
                      </>
                    )}
                    </tbody>
                    
                    
                    </table>
               </div>
             </div>

            
             
           
        </div>
        

        
         
        </>
    );

}

export default GetVideos;