import React, { useRef,useState } from "react";

import Pill from "./Pill";
import useScreenRecorder from "use-screen-recorder";
import swal from 'sweetalert';
// import { ReactSession } from 'react-client-session';
import Axios from "axios";
import { useHistory } from "react-router-dom";
import Home from "./Home";

const ScreenRecorder = () => {
  
  const {
    startRecording,
    pauseRecording,
    blobUrl,
    blob,
    resetRecording,
    resumeRecording,
    status,
    stopRecording,
  } = useScreenRecorder({ audio: true ,
                          video:true});

  const videoRef = useRef();
  const history = useHistory();
  function uploadVideo(e) {
    e.preventDefault();
    let sessionUserId = localStorage.getItem("userId");
    var fileName = Date.now() + '-' +'screenRecord.mp4';
    var wavefilefromblob = new File([blob],fileName,{mimeType: "video/mp4"});
    const formData = new FormData();

    formData.append('user', sessionUserId);
    formData.append('filename', fileName);
    formData.append('file', wavefilefromblob);
    
    const config = {
        headers: {
            'Content-Type': 'multipart/form-data'
            
        } 
    }
    Axios.post('http://localhost:3001/api/uploadVideo', formData, config).then((response)=> {
        // console.log("Video Uploaded Correctly")
        //console.log("status"+response.status);
        if(response.status == 200){
          swal("Successfully saved", "", "success");
          history.push("/getvideos");
        }
      }).catch((err)=> {
        console.log(err)
      })
  }
  return (
    <> 
    <Home/>
    <p>User name : { localStorage.getItem("userName") }</p>
    <div id="container">
      <div className="wrapper">
        <div className="pills">
          <Pill title="Status" value={status} />
          <Pill
            style={{ flexGrow: 1 }}
            title="Blob URL"
            value={blobUrl || "Waiting..."}
          />
        </div>

        <div>
          <video
            width={'300px'}
            ref={videoRef}
            src={blobUrl}
            poster={process.env.PUBLIC_URL + "/poster.png"}
            controls
            autoPlay


          />
        </div>

        <div className="buttons">
          {(status === "idle" || status === "error") && (
            <button onClick={startRecording}>Start recording</button>
          )}
          {(status === "recording" || status === "paused") && (
            <button onClick={stopRecording}>Stop recording</button>
          )}
          {(status === "recording" || status === "paused") && (
            <button
              onClick={() =>
                status === "paused" ? resumeRecording() : pauseRecording()
              }
            >
              {status === "paused" ? "Resume recording" : "Pause recording"}
            </button>
          )}
          {status === "stopped" && (
            <>
            <button
              onClick={() => {
                resetRecording()
                videoRef.current.load()
                
              }}
            >
              Reset recording
            </button>

            <button
              onClick={uploadVideo}
            >
              Upload Recording
            </button>
            </>
          )}
        </div>
      </div>
       
    </div>


    {/* <form>
                    <label>
                        Upload a file: <br /><br />
                        <input type="file" name="file" onChange={onChangeHandler} />
                    </label>
                    <br /><br />
                    <button type="submit">
                        Upload
                    </button>
      </form> */}
    </>
  );
};
export default ScreenRecorder;