import React from 'react';
import Webcam from "react-webcam";
// import { ReactSession } from 'react-client-session';
import Axios from "axios";
import swal from 'sweetalert';
import { useHistory } from "react-router-dom";
import Home from "./Home";

const WebRecorder = () => {
    const webcamRef = React.useRef(null);
    const mediaRecorderRef = React.useRef(null);
    const [capturing, setCapturing] = React.useState(false);
    const [recordedChunks, setRecordedChunks] = React.useState([]);
    const history = useHistory();
  
    const handleStartCaptureClick = React.useCallback(() => {
      setCapturing(true);
      mediaRecorderRef.current = new MediaRecorder(webcamRef.current.stream, {
        mimeType: "video/webm"
      });
      mediaRecorderRef.current.addEventListener(
        "dataavailable",
        handleDataAvailable
      );
      mediaRecorderRef.current.start();
    }, [webcamRef, setCapturing, mediaRecorderRef]);
  
    const handleDataAvailable = React.useCallback(
      ({ data }) => {
        if (data.size > 0) {
          setRecordedChunks((prev) => prev.concat(data));
        }
      },
      [setRecordedChunks]
    );
  
    const handleStopCaptureClick = React.useCallback(() => {
      mediaRecorderRef.current.stop();
      setCapturing(false);
    }, [mediaRecorderRef, webcamRef, setCapturing]);
  
    const handleDownload = React.useCallback(() => {
      if (recordedChunks.length) {
        const blob = new Blob(recordedChunks, {
          type: "video/webm"
        });
        const url = URL.createObjectURL(blob);
        const a = document.createElement("a");
        document.body.appendChild(a);
        a.style = "display: none";
        a.href = url;
        a.download = "react-webcam-stream-capture.webm";
        a.click();
        window.URL.revokeObjectURL(url);
        setRecordedChunks([]);
      }
    }, [recordedChunks]);


    const uploadVideo = (e) =>{
      e.preventDefault();
      let sessionUserId = localStorage.getItem("userId");
      var fileName = Date.now() + '-' +'web-cam-recorder.mp4';
      const blob = new Blob(recordedChunks, {
        type: "video/mp4"
      });
      var wavefilefromblob = new File([blob],fileName,{mimeType: "video/mp4"});
      const formData = new FormData();
  
      formData.append('user', sessionUserId);
      formData.append('filename', fileName);
      formData.append('file', wavefilefromblob);
      
      const config = {
          headers: {
              'Content-Type': 'multipart/form-data'
              
          } 
      }
        Axios.post('http://localhost:3001/api/uploadVideo', formData, config).then((response)=> {
          // console.log("Video Uploaded Correctly")
          //console.log(response.status);
          if(response.status == 200){
            swal("Successfully saved", "", "success");
            history.push("/getvideos");
          }
        }).catch((err)=> {
          console.log(err)
        })
    }
    
    const videoConstraints = {
      width: 1280,
      height: 720,
      facingMode: "user"
    };
    
    const capture = React.useCallback(
      () => {
        const imageSrc = webcamRef.current.getScreenshot();
        console.log("screenshot url::"+imageSrc);
      },
      [webcamRef]
    );

    


  return (
      <>
       <Home/>
      <div className="img_center">

        <Webcam audio={true} ref={webcamRef}  screenshotFormat="image/jpeg"
        width={650}
        />
      </div>
      <div className="img_center">
        {capturing ? (
          <button onClick={handleStopCaptureClick}>Stop Recording</button>
        ) : (
          <button onClick={handleStartCaptureClick}>Start Recording</button>

        )}
        {/* <button onClick={capture}>Capture photo</button> */}
        {recordedChunks.length > 0 && (
          <>
          {/* <button onClick={handleDownload}>Download Recording</button> */}
          <button onClick={uploadVideo}>Upload Recording </button>
          </>
        )}
        {/* <div>Hello</div> */}
      </div>
      </>
    );
  }
  
export default WebRecorder;
  